@import url(https://fonts.googleapis.com/css?family=Noto+Sans+JP:300,400,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  text-align: center;
  background-color: #fff0f0;
  font-family: "Noto Sans JP", Helvetica, sans-serif;
}
.App {
  padding: 2em 0;
}
.award_list {
  text-align: left;
  margin: 0 22%;
  z-index: 0;
}
.award--container {
  margin: 2em 0;
}
.award--header {
  display: grid;
  grid-template-columns: 70% 30%;
  position: relative;
  margin: 0 0 1em 0;
}
.award--title {
  margin: 0.8em 0 0 0;
  display: inline-block;
  color: #ff80a3;
  font-weight: 500;
  font-size: 1.6em;
  line-height: 1em;
}
.award--archive_link {
  position: absolute;
  right: 0;
  bottom: 0;
  color: #50bbe2;
  z-index: 0;
}
footer {
  margin: 4em 0 0 0;
  font-size: 1em;
}
.footer--credit_link {
  color: #50bbe2;
}

.search {
  position: relative;
  grid-area: 1/3 /2 /3;
}
.search--result_item_container {
  position: relative;
  top: 4.2em;
  left: -5.2em;
  width: 20em;
  max-height: 80vh;
  overflow-y: scroll;
  background-color: #ffee9d;
  border-radius: 1em;
  border: solid 1px #ff80a3;
  z-index: 1;
}
.search--result_item {
  position: relative;
  padding: 0.5em;
  cursor: pointer;
  border-width: 0 1px 1px 1px;
  text-align: left;
  z-index: 1;
}
.search--result_item:hover {
  background-color: #fff6c6;
}
.search--result_item_link {
  display: grid;
  grid-template-columns: 4.5em auto;
  grid-template-rows: 0 auto;
  text-decoration: none;
}
.search--result_item_img {
  grid-area: 2/ 1/ 2/ 2;
  height: 4.5em;
  width: 4.5em;
  border-radius: 50%;
}
.search--result_item_header_container {
  grid-area: 1/ 2/ 2 / span 2;
  padding: 0 0 0 0.2em;
}
.search--result_item_type_header {
  margin: 0;
  color: #50bbe2;
  font-weight: 500;
}
.search--result_item_main_header {
  margin: 0;
  color: #50bbe2;
  font-weight: 500;
  line-height: 1.3em;
}
.search--result_item_artist,
.search--result_item_ft,
.search--result_item_type,
.search--result_item_year {
  margin: 0;
  color: #50bbe2;
  font-size: 0.7em;
  font-weight: 400;
  line-height: 1.3em;
}
.search--result_item_ft {
  margin: 0;
  color: #50bbe2;
  font-weight: 400;
}

.header {
  display: grid;
  grid-template: 4em 3em/ 22% 56% 32%;
  height: 7em;
  z-index: 1;
}
.header--site_title_wrapper {
  grid-area: 1 / 2 / 2 / 2;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.header--site_title {
  color: #ff80a3;
  text-decoration: none;
  font-size: 1.7em;
  font-weight: 300;
  line-height: 1em;
}

.header--main_filter_container {
  display: flex;
  justify-self: center;
  grid-area: 2/ 2/ 3/ 3;
}
.header--type_link_wrapper {
  display: flex;
  flex-direction: row;
  justify-self: center;
  margin: auto 0;
}
.header--type_link {
  margin: 0 1em;
  display: inline-block;
  font-size: 0.8em;
  text-decoration: none;
  color: #50bbe2;
}
.header--search_bar_container {
  position: absolute;
  bottom: 0;
}
.header--search_input {
  margin: auto auto 0 0;
  background-color: transparent;
  border: solid #ff80a3;
  border-width: 0 0 1px 0;
  font-size: 1em;
  line-height: 1em;
}
.header--search_input:focus {
  outline: none;
}

.card--container {
  padding: 0.5em 0;
  display: grid;
  grid-template-columns: 6em auto;
  height: 6em;
  text-align: left;
}

.card--img {
  margin: 0 0.5em 0 0;
  height: 5.5em;
  width: 5.5em;
  border-radius: 50%;
}
.card--header_container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.card--main_header {
  color: #ff80a3;
  margin: 0;
  line-height: 1em;
  font-weight: 300;
}
.card--ft {
  margin: 0.2em 0 0.3em 0;
  color: #ff80a3;
  font-weight: 300;
}

.card--artist {
  margin: 0;
  font-weight: 300;
}
.won .card--main_header,
.won .card--ft,
.won .card--artist {
  font-weight: 500;
}

